<template>
  <div class="vr">
    <div id="webglviewer"></div>
  </div>
</template>

<style>
  #webglviewer {
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 100;
  }
</style>

<script>
  import THREE from 'three'
  import '../assets/js/lib/StereoEffect'
  import EaselJS from 'createjs-collection'

  export default{
    data () {
      return {
        msg: 'hello vue'
      }
    },
    ready () {
      var scene
      var camera
      var canvas2
      var renderer
      var element
      var container
      var effect
      var video
      var canvas
      var context
      var texture
      var themes = ['blackandwhite', 'sepia', 'arcade', 'inverse']
      var currentTheme = 0
      var lookingAtGround = false
      var stage

      init()

      function init () {
        scene = new THREE.Scene()
        camera = new THREE.PerspectiveCamera(90, window.innerWidth / window.innerHeight, 0.001, 700)
        camera.position.set(0, 15, 0)
        scene.add(camera)

        renderer = new THREE.WebGLRenderer()
        element = renderer.domElement
        container = document.getElementById('webglviewer')
        container.appendChild(element)

        effect = new THREE.StereoEffect(renderer)

        element.addEventListener('click', fullscreen, false)

        if (window.DeviceOrientationEvent) {
          window.addEventListener('deviceorientation', function (evt) {
            if (evt.gamma > -1 && evt.gamma < 1 && !lookingAtGround) {
              lookingAtGround = true
              currentTheme = (themes.length > currentTheme + 1) ? currentTheme + 1 : 0

              setTimeout(function () {
                lookingAtGround = false
              }, 4000)
            }
          })
        }

        video = document.createElement('video')
        video.setAttribute('autoplay', true)

        var options = {
          video: {
            optional: [{facingMode: 'environment'}]
          }
        }

        navigator.getUserMedia = (navigator.getUserMedia ||
        navigator.webkitGetUserMedia ||
        navigator.mozGetUserMedia ||
        navigator.msGetUserMedia)

        if (typeof MediaStreamTrack === 'undefined' && navigator.getUserMedia) {
          window.alert('This browser doesn\'t support this demo :(')
        } else {
          navigator.mediaDevices.enumerateDevices()
            .then(function (sources) {
              for (var i = 0; i !== sources.length; ++i) {
                var source = sources[i]
                if (source.kind === 'video') {
                  if (source.facing && source.facing === 'environment') {
                    options.video.optional.push({'sourceId': source.id})
                  }
                }
              }

              navigator.getUserMedia(options, streamFound, streamError)
            })
        }

        function streamFound (stream) {
          document.body.appendChild(video)
          video.src = window.URL.createObjectURL(stream)
          video.style.width = '100%'
          video.style.height = '100%'
          video.play()

          canvas = document.createElement('canvas')
          canvas.width = video.clientWidth
          canvas.height = video.clientHeight
          canvas.width = nextPowerOf2(canvas.width)
          canvas.height = nextPowerOf2(canvas.height)

          canvas2 = document.createElement('canvas')
          canvas2.width = video.clientWidth
          canvas2.height = video.clientHeight
          canvas2.width = nextPowerOf2(canvas2.width)
          canvas2.height = nextPowerOf2(canvas2.height)

          stage = new EaselJS.Stage(canvas2)
          let circle = new EaselJS.Shape()
          let buttons = new EaselJS.Shape()
          let xCenter = canvas.width / 2
          let yCenter = canvas.height / 2

          circle.graphics
            .setStrokeStyle(600)
            .beginRadialGradientStroke(['rgba(0, 0, 0, 0)', 'black'], [0, 0.5], xCenter, yCenter, 150, xCenter, yCenter, 600)
            .drawCircle(xCenter, yCenter, 150)

          buttons.graphics
            .setStrokeStyle(3)
            .beginStroke('#fff')
            .drawRect(xCenter * 0.7, yCenter + 100, 150, 70)
            .drawRect(xCenter * 1.2, yCenter + 100, 150, 70)

          stage.addChild(circle)
          stage.addChild(buttons)

          window.addEventListener('devicemotion', (event) => {
            console.log(event)
          })

          function nextPowerOf2 (x) {
            return Math.pow(2, Math.ceil(Math.log(x) / Math.log(2)))
          }

          context = canvas.getContext('2d')
          texture = new THREE.Texture(canvas)
          texture.context = context

          // If you do not use powersOf2, or you want to adjust things more, you could use these:
//          texture.minFilter = THREE.LinearMipMapLinearFilter
//          texture.magFilter = THREE.NearestFilter

          var cameraPlane = new THREE.PlaneGeometry(1920, 1080)

          var cameraMesh = new THREE.Mesh(cameraPlane, new THREE.MeshBasicMaterial({
            color: 0xffffff, opacity: 1, map: texture
          }))
          cameraMesh.position.z = -600

          scene.add(cameraMesh)
          animate()
        }

        function streamError (error) {
          console.log('Stream error: ', error)
        }
      }

      function animate () {
        if (context) {
          context.drawImage(video, 0, 0, canvas.width, canvas.height)
          stage.update()
          context.drawImage(canvas2, 0, 0, canvas.width, canvas.height)

          if (video.readyState === video.HAVE_ENOUGH_DATA) {
            texture.needsUpdate = true
          }

          window.requestAnimationFrame(animate)

          update()
          render()
        }
      }

      function resize () {
        var width = container.offsetWidth
        var height = container.offsetHeight

        camera.aspect = width / height
        camera.updateProjectionMatrix()

        renderer.setSize(width, height)
        effect.setSize(width, height)
      }

      function update (dt) {
        resize()

        camera.updateProjectionMatrix()
      }

      function render (dt) {
        effect.render(scene, camera)
      }

      function fullscreen () {
        if (container.requestFullscreen) {
          container.requestFullscreen()
        } else if (container.msRequestFullscreen) {
          container.msRequestFullscreen()
        } else if (container.mozRequestFullScreen) {
          container.mozRequestFullScreen()
        } else if (container.webkitRequestFullscreen) {
          container.webkitRequestFullscreen()
        }
      }
    }
  }
</script>



